<template>
  <div class="left-chart-1" v-if="isShow">
    <div class="lc1-header">当日工艺情况汇总</div>
    <!-- <div class="lc1-details" style="margin: 0 auto"><span>5500</span></div> -->
    <div>
      <div class="water-top">
        <dv-water-level-pond v-if="showWater" :config="water1" style="margin: 0 auto" />
      </div>
      <!-- <div class="water-bottom">
        <p>喷码</p>
        <dv-water-level-pond :config="water2" style="width: 70%" />
      </div> -->
    </div>
    <div style="width: 150px; margin: 10px auto 0px; text-align: center">
      印刷期望值({{ hopeShua }})
    </div>
    <dv-decoration-2 style="height: 15px; margin-top: 15px; display: block;" />
    <!-- <dv-capsule-chart style="width: 95%" class="lc1-chart" :config="config" /> -->
    <div class="new-bottom">
      <div class="bottom-div" v-for="(item, index) in newBottomList" :key="index">
        <p class="first-p">{{item.name}}</p>
        <div class="bottom-item">
          <div :style="'width:' + item.percent + '%' + ';' + 'background-color:' + item.color">
            <p>{{item.percent}}%</p>
          </div>
        </div>
        <p class="second-p">{{item.planValue}}</p>
      </div>
    </div>
    <!-- <dv-decoration-2 style="height:10px;" /> -->
    <!-- <dv-capsule-chart class="lc3-chart" :config="config" /> -->
    <div class="left-bottom1">
      <!-- <div class="bottom-cont">
        <el-progress :percentage="50"></el-progress>
      </div> -->
      <!-- v-for="(item,index) in progressList" :key="index" -->
      <!-- :color="setColor(index)" -->
      <!-- <div class="bottom-cont" v-for="(item,index) in progressList" :key="index">
        <p>{{item.serviceName}}</p>
        <el-progress
        :text-inside="true"
        :stroke-width="20"
        :color="setColor(index)"
        :percentage="setItemProgress(item)"
        :format="setItemText(item)"
        ></el-progress>
      </div> -->
    </div>
  </div>
</template>

<script>
export default {
  name: "LeftChart1",
  props: {
    leftData: {
      type: Array,
      default: [],
    },
  },
  data() {
    return {
      water1: {
        showWater: false,
        //印刷水位图默认为0 当有值传入时再处理
        data: [0],
        shape: "round",
        waveHeight: 25,
        waveNum: 2,
      },
      water2: {
        data: [66, 45],
        shape: "round",
      },
      config: {
        data: [],
        showValue: true,
      },
      isShow: false,
      hopeShua: 0,
      customColor: [
        "#f56c6c",
        "#e6a23c",
        "#5cb87a",
        "#1989fa",
        "#6f7ad3",
        "#f56c6c",
        "#e6a23c",
        "#5cb87a",
        "#1989fa",
        "#6f7ad3",
        "#f56c6c",
        "#e6a23c",
        "#5cb87a",
      ],
      //柱状图渲染数组
      progressList: [],
      newBottomList: [],
    };
  },
  mounted() {
    this.updateData();
  },
  watch: {
    leftData: {
      handler(newVal, oldVal){
          if(newVal){
            this.updateData()
          }
      },
      deep: true
    }
  },
  methods: {
    updateData() {
      //初始化清空config.data
      // this.config.data = []
      let configDataMock = [];
      //处理印刷百分比数据
      let hopeNum = 0;
      let realNum = 0;
      this.leftData.forEach((item, index) => {
        if (item.serviceName == "印刷") {
          hopeNum = item.planQuantity;
          this.hopeShua = hopeNum;
          realNum = item.actualQuantity;
        } else {
          let per = item.actualQuantity/item.planQuantity*100
          per = per.toFixed(2)
          let obj = {
            name: null,
            value: null,
            color: '',
            planValue: null,
            percent: null
          };
          obj.name = item.serviceName;
          obj.value = item.actualQuantity;
          //新加需求，加入颜色和计算百分比
          obj.color = this.customColor[index]
          obj.planValue = item.planQuantity
          obj.percent = per
          configDataMock.push(obj);
        }
      });
      //生成印刷百分比数字
      let persent = (realNum / hopeNum) * 100;
      // persent = persent.toFixed(2)
      persent = parseInt(persent);
      if(hopeNum != 0){
           this.water1.data = [];
           this.water1.data.push(persent);
      }
      this.showWater = false
      this.showWater = true
      // configDataMock.forEach((item, index) => {
      //   //单独抽离印刷数据
      //   if (item.name == "印刷") {
      //     //删除印刷数据
      //     configDataMock.splice(index, 1);
      //     // hopeNum = item.planQuantity
      //     // realNum = item.actualQuantity
      //   }
      // });
      this.config.data = configDataMock;
      this.newBottomList = configDataMock
      //给进度条图数组赋值
      this.progressList = this.leftData;
      this.isShow = true;
    },
    setItemProgress(data) {
      if (data.actualQuantity > data.planQuantity) {
        return 100;
      } else {
        return parseInt(
          (data.actualQuantity / data.planQuantity).toFixed(1) * 100
        );
      }
    },
    setItemText(data) {
      return () => {
        return +data.actualQuantity + " / " + data.planQuantity;
      };
    },
    setColor(index) {
      return this.customColor[index];
    },
  },
};
</script>

