import CenterCmp from './CenterCmp'
import BottomCharts from './BottomCharts'

export default {
  name: 'DataView',
  components: {
    LeftChart1,
    CenterCmp,
    BottomCharts
  },
  data() {
    return {
      allData: null,
      sonRefresh: false,
      leftData: [],
      topData: null,
      bottomData: []
    }
  },
  // watch: {
  //   allData: {
  //     handler(newVal, oldVal){
  //       if(newVal){
  //         this.leftData = this.allData.flowInfoVOList
  //     this.topData = this.allData
  //     this.bottomData = this.allData.produceInfoVOList
  //     this.$forceUpdate()
  //       }
  //     },
  //     deep:true,
  //   }
  // },
  async mounted() {
    let test = window.location.href
    let port = window.location.port
    console.log('浏览器宽度:' + window.screen.width * window.devicePixelRatio)
    console.log('浏览器高度:' + window.screen.height * window.devicePixelRatio)
    await this.getData()
    await this.refreshFlag()
    clearInterval(myTimeDisplay)
    let myTimeDisplay = setInterval(() => {
      this.getData()
      this.refreshFlag() //每分钟调用一次
    }, 60000)
  },
  methods: {
    async getData() {
      let date = this.formatDate(new Date(), 'yyyy-MM-dd')
      let { data: res } = await this.axios.get(
        `http://192.168.101.254:10001/screen/getScreenData?date=${date}`
      )
      this.allData = null
      this.allData = res.data
      //mock数据
      // this.allData = {
      //   orderNum: 5000,
      //   printNum: [
      //     { machineName: 'CD 102-5+L', num: 50000 },
      //     { machineName: 'CD 102-8+L', num: 50000 },
      //     { machineName: 'CX 104-7+L', num: 20000 },
      //     { machineName: 'XL 162-4', num: 65000 }
      //   ],
      //   changeNum: [
      //     { machineName: 'CD 102-5+L', num: 200 },
      //     { machineName: 'CD 102-8+L', num: 100 },
      //     { machineName: 'CX 104-7+L', num: 300 },
      //     { machineName: 'XL 162-4', num: 400 }
      //   ],
      //   printNumMonth: [
      //     { month: '1月', num: 2860292 },
      //     { month: '2月', num: 2860292 }
      //   ],
      //   machineSpeed: [
      //     { machineName: 'CD 102-5+L', speed: 4000 },
      //     { machineName: 'CD 102-8+L', speed: 5000 },
      //     { machineName: 'CX 104-7+L', speed: 2000 },
      //     { machineName: 'XL 162-4', speed: 5000 }
      //   ]
      // }
      this.topData = this.allData
      this.bottomData = this.allData.machineSpeed
    },
    refreshFlag() {
      this.sonRefresh = false
      this.sonRefresh = true
    },
    formatDate(date, fmt) {
      //获取年份
      if (/(y+)/.test(fmt)) {
        // 把数字变成字符串
        let dateY = date.getFullYear() + ''
        //RegExp.$1 在判断中出现过，且是括号括起来的，所以 RegExp.$1 就是 "yyyy"
        fmt = fmt.replace(RegExp.$1, dateY.substr(4 - RegExp.$1.length))
      }

      //获取其他
      let o = {
        'M+': date.getMonth() + 1,
        'd+': date.getDate(),
        'h+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds()
      }
      for (const k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
          let str = o[k] + ''
          fmt = fmt.replace(
            RegExp.$1,
            RegExp.$1.length == 1 ? str : this.padLeftZero(str)
          )
        }
      }
      return fmt
    },
    padLeftZero(str) {
      return ('00' + str).substr(str.length)
    }
  }
}
</script>

