<script>
export default {
  name: 'CenterCmp',
  props: {
    topData: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      option4: {},
      option4Copy: {
        xAxis: {
          data: 'value',
          min: 0,
          axisLabel: {
            formatter({ value }) {
              return Math.round(value / 100) * 100
            },
            style: {
              fill: '#999'
            }
          },
          axisLine: {
            style: {
              stroke: '#999'
            }
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          splitLine: {
            show: false
          },
          data: [],
          axisLabel: {
            style: {
              fill: '#999'
            }
          },
          axisLine: {
            style: {
              stroke: '#999'
            }
          },
        },
        series: [
          {
            data: [],
            type: 'bar',
            gradient: {
              color: ['#37a2da', '#67e0e3']
            },
            animationCurve: 'easeOutBack'
          }
        ]
      },
      option5: {},
      option5Copy: {
        xAxis: {
          data: 'value',
          min: 0,
          axisLabel: {
            formatter({ value }) {
              return Math.round(value / 10) * 10
            },
            style: {
              fill: '#999'
            }
          },
          axisLine: {
            style: {
              stroke: '#999'
            }
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          splitLine: {
            show: false
          },
          data: [],
          axisLabel: {
            style: {
              fill: '#999'
            }
          },
          axisLine: {
            style: {
              stroke: '#999'
            }
          }
        },
        series: [
          {
            data: [],
            type: 'bar',
            animationCurve: 'easeOutBack',
            gradient: {
              color: ['#37a2da', '#67e0e3']
            }
          }
        ]
      },
      option: {
        legend: {
          textStyle: {
            fill: '#fff'
          }
        },
        xAxis: {
          data: [],
          //边界间隔
          // boundaryGap: false,
          axisLine: {
            style: {
              stroke: '#999'
            }
          },
          axisLabel: {
            style: {
              fill: '#999'
            }
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          data: 'value',
          splitLine: {
            show: false
          },
          axisLine: {
            style: {
              stroke: '#999'
            }
          },
          axisLabel: {
            style: {
              fill: '#999'
            },
            formatter({ value }) {
              // return value.toFixed(2);
              return Math.round(value / 100) * 100
            }
          },
          axisTick: {
            show: false
          },
          min: 5000000,
          max:  40000000
        },
        series: [
          {
            data: [],
            type: 'line',
            name: '设备完好率',
            //光滑曲线 长度大于三的时候才能展示曲线
            // smooth: true,
            lineArea: {
              show: true,
              gradient: ['rgba(55, 162, 218, 0.6)', 'rgba(55, 162, 218, 0)']
            },
            linePoint: {
              show: true,
              radius: 4,
              style: {
                fill: '#00db95'
              }
            }
          }
        ]
      },
      times: '',
      timer: null,
      isShow: false,
      //订单内盒
      orderNumIn: 0,
      //订单外箱
      orderNumOut: 0,
      //订单总数
      orderAll: 0,
      //工单内盒
      workNumIn: 0,
      //工单外箱
      workNumOut: 0,
      //工单总数
      workAll: 0,
      //头部总数展示数据
      arrNum: []
    }
  },
  created() {
    // this.getTime()
    this.updateTime()
  },
  watch: {
    topData: {
      handler(newVal, oldVal) {
        if (newVal) {
          this.updateData()
        }
      },
      deep: true
    }
  },
  mounted() {
    this.updateData()
  },
  //销毁定时器
  beforeDestroy: function () {
    if (this.timer) {
      // console.log("销毁定时器");
      clearTimeout(this.timer) //在Vue实例销毁前，清除定时器
    }
  },
  methods: {
    updateData() {
      //首次渲染清空折线图，柱状图横坐标纵坐标数据
      this.option.xAxis.data = []
      this.option.series[0].data = []
      // this.option4.yAxis.data = []
      // this.option4.series[0].data = []
      // this.option5.yAxis.data = []
      // this.option5.series[0].data = []
      //option4和5深拷贝 使得每次调用该方法时option都为空，让图形重新渲染
      this.option4 = {}
      this.option5 = {}
      this.option4 = JSON.parse(JSON.stringify(this.option4Copy))
      this.option5 = JSON.parse(JSON.stringify(this.option5Copy))
      // let orderInfoVO = this.topData.orderInfoVO;
      // this.orderNumIn = orderInfoVO.orderNumIn;
      // this.orderNumOut = orderInfoVO.orderNumOut;
      // this.orderAll = this.orderNumIn + this.orderNumOut;
      // this.workNumIn = orderInfoVO.workNumIn;
      // this.workNumOut = orderInfoVO.workNumOut;
      // this.workAll = this.workNumIn + this.workNumOut;
      // //顶部订单总数数据处理
      // //将订单总量数字转为字符串再转为数据，然后遍历展示
      this.arrNum = this.topData.orderNum.toString().split('')
      //订单量月趋势处理
      let orderMon = this.topData.printNumMonth
      let yAxisList = []
      //判断折线图数组长度，若为1则显示单点 若大于1则贴近y轴显示多点
      if (orderMon.length <= 1) {
        this.option.xAxis.boundaryGap = true
      } else {
        this.option.xAxis.boundaryGap = false
      }
      //判断折线图数组长度，若小于等于二则显示直线 若大于2则显示曲线
      if (orderMon.length <= 2) {
        this.option.series[0].smooth = false
      } else {
        this.option.series[0].smooth = false
      }
      orderMon.forEach((item) => {
        this.option.xAxis.data.push(item.month)
        this.option.series[0].data.push(item.num)
        yAxisList.push(item.num)
        //  this.option.series.data.push(item.orderToldNum)
      })
      //遍历纵坐标值组成的数组，取出最大值作为纵轴最大值, 取出最小值作为纵轴最小值
      // let yAxisMax = Math.max.apply(null, yAxisList)
      // this.option.yAxis.max = Math.max.apply(null, yAxisList) + 1000
      // this.option.yAxis.min = Math.min.apply(null, yAxisList) - 1000
      //印张数,换版数柱状图处理
      let printNum = this.topData.printNum
      let changeNum = this.topData.changeNum
      //印张数x轴最大值数组收集
      let xMaxListPrint = []
      //换版数x轴最大值数组收集
      let xMaxListChange = []
      printNum.forEach((item) => {
        this.option4.yAxis.data.push(item.machineName)
        this.option4.series[0].data.push(item.num)
        xMaxListPrint.push(item.num)
      })
      this.option4.xAxis.max = Math.max.apply(null, xMaxListPrint)
      changeNum.forEach((item) => {
        this.option5.yAxis.data.push(item.machineName)
        this.option5.series[0].data.push(item.num)
        xMaxListChange.push(item.num)
      })
      this.option5.xAxis.max = Math.max.apply(null, xMaxListChange)
      this.isShow = true
    },
    getTime() {
      let now = new Date()
      let days = function (val) {
        if (!val) return ''
        const date = new Date(val)
        const Y = date.getFullYear() + '-'
        const M =
          (date.getMonth() + 1 < 10
            ? '0' + (date.getMonth() + 1)
            : date.getMonth() + 1) + '-'
        const D =
          date.getDate() > 9 ? date.getDate() + ' ' : '0' + date.getDate() + ' '
        const h =
          date.getHours() > 9
            ? date.getHours() + ':'
            : '0' + date.getHours() + ':'
        const m =
          date.getMinutes() > 9
            ? date.getMinutes() + ':'
            : '0' + date.getMinutes() + ':'
        const s =
          date.getSeconds() > 9 ? date.getSeconds() : '0' + date.getSeconds()
        return Y + M + D + h + m + s
      }
      this.times = days(now)
    },

    //定时器函数
    updateTime() {
      this.getTime()
      this.timer = setTimeout(() => {
        this.updateTime()
      }, 1000)
    }
  }
}
</script>

